<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="6">
        <v-row>
          <v-col cols="6">
            <v-card
              :disabled="loadingRecords"
              class="border-bottom-primary"
              @click="showTickets(1, 'Unassigned')"
            >
              <v-list-item>
                <v-list-item-content class="text-center">
                  <v-list-item-title class="text-h5 mb-1">
                    {{ unassigned | addComma }}
                  </v-list-item-title>
                  <v-list-item-subtitle>Unassigned</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>

          <v-col cols="6">
            <v-card
              :disabled="loadingRecords"
              class="border-bottom-primary"
              @click="showTickets(2, 'Open')"
            >
              <v-list-item>
                <v-list-item-content class="text-center">
                  <v-list-item-title class="text-h5 mb-1">
                    {{ open | addComma }}
                  </v-list-item-title>
                  <v-list-item-subtitle>Open</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>

          <v-col cols="6">
            <v-card
              :disabled="loadingRecords"
              class="border-bottom-primary"
              @click="showTickets(3, 'Pending')"
            >
              <v-list-item>
                <v-list-item-content class="text-center">
                  <v-list-item-title class="text-h5 mb-1">
                    {{ pending | addComma }}
                  </v-list-item-title>
                  <v-list-item-subtitle>Pending</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>

          <v-col cols="6">
            <v-card
              :disabled="loadingRecords"
              class="border-bottom-primary"
              @click="showTickets(6, 'On Hold')"
            >
              <v-list-item>
                <v-list-item-content class="text-center">
                  <v-list-item-title class="text-h5 mb-1">
                    {{ onHold | addComma }}
                  </v-list-item-title>
                  <v-list-item-subtitle>On Hold</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
        </v-row>
      </v-col>

      <v-col cols="12" md="3">
        <v-row>
          <v-col cols="6" md="12">
            <v-card
              :disabled="loadingRecords"
              class="border-bottom-success"
              @click="showTickets(4, 'Resolved')"
            >
              <v-list-item>
                <v-list-item-content class="text-center">
                  <v-list-item-title class="text-h5 mb-1">
                    {{ resolved | addComma }}
                  </v-list-item-title>
                  <v-list-item-subtitle>Resolved</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>

          <v-col cols="6" md="12">
            <v-card
              :disabled="loadingRecords"
              class="border-bottom-success"
              @click="showTickets(5, 'Closed')"
            >
              <v-list-item>
                <v-list-item-content class="text-center">
                  <v-list-item-title class="text-h5 mb-1">
                    {{ closed | addComma }}
                  </v-list-item-title>
                  <v-list-item-subtitle>Closed</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
        </v-row>
      </v-col>

      <v-col cols="12" md="3">
        <v-row>
          <v-col cols="6" md="12">
            <v-card
              :disabled="loadingRecords"
              class="border-bottom-error"
              @click="showTickets(104, 'Overdue')"
            >
              <v-list-item>
                <v-list-item-content class="text-center">
                  <v-list-item-title class="text-h5 mb-1">
                    {{ overdue | addComma }}
                  </v-list-item-title>
                  <v-list-item-subtitle>Overdue</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>

          <v-col cols="6" md="12">
            <v-card
              :disabled="loadingRecords"
              class="border-bottom-error"
              @click="showTickets(105, 'Due Today')"
            >
              <v-list-item>
                <v-list-item-content class="text-center">
                  <v-list-item-title class="text-h5 mb-1">
                    {{ dueToday | addComma }}
                  </v-list-item-title>
                  <v-list-item-subtitle>Due Today</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <v-row>
      <v-col>
        <v-card>
          <v-card-text>
            <v-row>
              <v-col cols="12" md="7">
                <v-frappe-chart
                  title="Ticket in the last 24H"
                  type="line"
                  :labels="labels"
                  :data="[{ values: values }]"
                />
              </v-col>

              <v-col cols="12" md="5">
                <div class="font-weight-bold ml-6 mt-2 mb-4">Tickets</div>

                <v-timeline align-top dense>
                  <v-timeline-item class="mr-5" color="grey" small>
                    <div
                      class="d-flex"
                      style="cursor: pointer"
                      @click="showTickets(101, 'Received')"
                    >
                      <div>Received</div>
                      <v-spacer></v-spacer>
                      <h2 class="black--text">{{ received | addComma }}</h2>
                    </div>
                    <v-divider></v-divider>
                  </v-timeline-item>

                  <v-timeline-item class="mr-5" color="primary" small>
                    <div
                      class="d-flex"
                      style="cursor: pointer"
                      @click="showTickets(102, 'Unresolved')"
                    >
                      <div>Unresolved</div>
                      <v-spacer></v-spacer>
                      <h2 class="black--text">{{ unresolved | addComma }}</h2>
                    </div>
                    <v-divider></v-divider>
                  </v-timeline-item>

                  <v-timeline-item class="mr-5" color="success" small>
                    <div
                      class="d-flex"
                      style="cursor: pointer"
                      @click="showTickets(103, 'Received')"
                    >
                      <div>Resolved</div>
                      <v-spacer></v-spacer>
                      <h2 class="black--text">
                        {{ resolvedAndClosed | addComma }}
                      </h2>
                    </div>
                    <v-divider></v-divider>
                  </v-timeline-item>
                </v-timeline>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-dialog v-model="ticketsDialog" max-width="800">
      <v-data-table
        :headers="ticketsHeaders"
        :items="tickets"
        :items-per-page="5"
        class="elevation-1"
        :loading="loadingDialog"
        loading-text="Loading... Please wait"
      >
        <template v-slot:top>
          <div class="d-flex px-3 py-2">
            <h3>{{ ticketsDialogTitle }}</h3>
            <v-spacer></v-spacer>
            <v-btn icon small @click="ticketsDialog = false">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </div>
          <v-divider></v-divider>
        </template>

        <template v-slot:[`item.client`]="{ item }">
          {{ item.client | nameFormat }}
        </template>

        <template v-slot:[`item.subject`]="{ item }">
          {{ item.subject }}
          <span class="grey--text">#{{ item.id }}</span>
        </template>

        <template v-slot:[`item.priority_status`]="{ item }">
          <v-chip :color="getPriorityColor(item.priority_status_id)" small dark>
            {{ item.priority_status }}
          </v-chip>
        </template>

        <template v-slot:[`item.created_at`]="{ item }">
          <span>{{ item.created_at | dateFormat }}</span>
        </template>

        <template v-slot:[`item.action`]="{ item }">
          <div v-show="$vuetify.breakpoint.mdAndUp ? true : false">
            <v-tooltip left>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  small
                  color="primary"
                  :to="{ name: 'MyTeamEdit', params: { id: item.id } }"
                >
                  <v-icon small color="primary" v-bind="attrs" v-on="on">
                    mdi-eye
                  </v-icon>
                </v-btn>
              </template>
              <span>View</span>
            </v-tooltip>
          </div>

          <div v-show="$vuetify.breakpoint.mdAndUp ? false : true">
            <v-btn
              color="primary"
              x-small
              :to="{ name: 'MyTeamEdit', params: { id: item.id } }"
            >
              View
            </v-btn>
          </div>
        </template>
      </v-data-table>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
import { VFrappeChart } from "vue-frappe-chart";
import moment from "moment";
import { capitalizeFirstLetterEachWord } from "@/helpers/formats";

export default {
  components: {
    VFrappeChart,
  },

  data: () => ({
    received: 0,
    unresolved: 0,
    resolvedAndClosed: 0,

    overdue: 0,
    dueToday: 0,

    unassigned: 0,
    open: 0,
    pending: 0,
    resolved: 0,
    closed: 0,
    onHold: 0,

    labels: [
      "12am-3am",
      "3am-6am",
      "6am-9am",
      "9am-12pm",
      "12pm-3pm",
      "3pm-6pm",
      "6pm-9pm",
      "9pm-12am",
    ],
    values: [0, 0, 0, 0, 0, 0, 0, 0],

    ticketsDialog: false,
    ticketsDialogTitle: "",
    ticketsHeaders: [
      { text: "Client", value: "client" },
      { text: "Subject", value: "subject" },
      { text: "Priority", value: "priority_status" },
      { text: "Date Created", value: "created_at" },
      { text: "Action", value: "action", sortable: false },
    ],
  }),

  created() {
    this.getRecords();
  },

  computed: {
    ...mapState("dashboard", [
      "loadingRecords",
      "records",
      "loadingDialog",
      "tickets",
    ]),
  },

  methods: {
    async getRecords() {
      await this.$store.dispatch("dashboard/getRecords");

      if (Object.keys(this.records).length) {
        this.received = this.records.received;
        this.unresolved = this.records.unresolved;
        this.resolvedAndClosed = this.records.resolvedAndClosed;

        this.overdue = this.records.overdue;
        this.dueToday = this.records.dueToday;

        this.unassigned = this.records.unassigned;
        this.open = this.records.open;
        this.pending = this.records.pending;
        this.resolved = this.records.resolved;
        this.closed = this.records.closed;
        this.onHold = this.records.onHold;

        this.values = this.records.values;
      }
    },

    showTickets(id, title) {
      this.$store.commit("dashboard/setTickets", []);
      this.$store.dispatch("dashboard/getTickets", id);
      this.ticketsDialogTitle = title;
      this.ticketsDialog = true;
    },

    getPriorityColor(data) {
      switch (data) {
        case 1:
          return "green";
        case 2:
          return "blue";
        case 3:
          return "orange";
        case 4:
          return "red";
        default:
          return "grey";
      }
    },
  },

  filters: {
    addComma(val) {
      return val.toLocaleString();
    },

    dateFormat(date) {
      return moment(date).format("MMM D, YYYY h:mm A");
    },

    nameFormat(val) {
      return capitalizeFirstLetterEachWord(val);
    },
  },
};
</script>

<style scoped>
.border-bottom-error {
  border-left: 5px solid #ff5252 !important;
}

.border-bottom-success {
  border-left: 5px solid #4caf50 !important;
}

.border-bottom-primary {
  border-left: 5px solid #1976d2 !important;
}
</style>